import { React, useState } from "react";
import Switch from "../../Switch/Switch";
import {
  SettingInfo,
  SettingLabel,
  SettingProps,
  SettingSubtitle,
  SettingTitle,
  SettingWrapper,
} from "../Settings.elements";

const Other = () => {
  const [value, setValue] = useState(false);

  return (
    <div>
      <SettingWrapper>
        <SettingInfo>
          <SettingTitle> Lorem Ipsum </SettingTitle>
          <SettingSubtitle>
            {" "}
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Quisque
            ultricies ex et nunc lacinia, et ullamcorper dui congue.{" "}
          </SettingSubtitle>
        </SettingInfo>
        <SettingProps>
          <SettingLabel> Lorem Ipsum</SettingLabel>
          <Switch
            isOn={value}
            onColor="#262952"
            handleToggle={() => setValue(!value)}
          />
        </SettingProps>
      </SettingWrapper>
    </div>
  );
};

export default Other;
