import styled, { createGlobalStyle, keyframes } from "styled-components";
import { FaMagento } from "react-icons/fa";

const GlobalStyle = createGlobalStyle`
    *,::before,::after{
      box-sizing: border-box;
    }

    body{
      position: relative;
      margin: 0;
      font-family: 'Poppins', sans-serif;
      font-size: 1rem;
      transition: .5s;
      color: #212529;
    }

    h1{
      margin: 0;
    }

    ul{
      margin: 0;
      padding: 0;
      list-style: none;
    }

    a{
      text-decoration: none;
    }
`;

export const disappear = keyframes`
    to {
        opacity: 0;
        display: none;
    }
`;

export const ErrorBox = styled.div`
  right: 0;
  left: 90px;
  margin: 32px auto;
  background-color: #f44336;
  padding: 20px;
  color: #fff;
  z-index: 9999;
  width: 400px;
  position: absolute;
  border-radius: 8px;
`;

// padding: 16px 16px 16px 108px;
export const Container = styled.div`
  margin: auto;
  width: 100%;
  height: 100vh;
  padding: 0px 0px 0px 92px;
`;

export const SiteIcon = styled(FaMagento)`
  font-size: 1.25rem;
  margin: 0.75rem;
  cursor: pointer;
`;
export default GlobalStyle;
