import { NavLink } from "react-router-dom";
import styled from "styled-components";
import { FaMagento } from "react-icons/fa";
import { FiHeart } from "react-icons/fi";
import {
  IoLogInOutline,
  IoExitOutline,
  IoHelpCircleOutline,
  IoSettingsOutline,
  IoCartOutline,
  IoMapOutline,
} from "react-icons/io5";

export const NavWrapper = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: ${({ expand }) => (expand ? "calc(92px + 9.25rem)" : "92px")};
  height: 100vh;
  background-color: rgb(248, 248, 248);
  color: rgb(88, 82, 128);
  padding: 1.5rem 1.5rem 2rem;
  transition: 0.5s;
  z-index: 100;
`;

export const Nav = styled.nav`
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
`;

export const NavBrand = styled.div`
  display: grid;
  grid-template-columns: max-content max-content;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
`;

export const NavLogoIcon = styled(FaMagento)`
  font-size: 1.25rem;
  margin: 0.75rem;
  cursor: pointer;
`;

export const NavLogoText = styled.a`
  color: rgb(88, 82, 128);
  font-weight: 600;
`;

export const NavUILink = styled(NavLink)`
  display: grid;
  grid-template-columns: max-content max-content;
  align-items: center;
  column-gap: 0.75rem;
  padding: 0.75rem;
  color: rgb(88, 82, 128);
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  transition: 0.3s;
  cursor: pointer;
  background-color: ${({ active }) =>
    active ? "rgb(223,223,223)" : "inherit"};

  &:hover {
    background-color: rgb(223, 223, 223);
  }

  &.active {
    background-color: rgb(223, 223, 223);
  }
`;

export const NavUiLinkQuit = styled.div`
  display: grid;
  grid-template-columns: max-content max-content;
  align-items: center;
  column-gap: 0.75rem;
  padding: 0.75rem;
  color: rgb(88, 82, 128);
  border-radius: 0.5rem;
  margin-bottom: 1rem;
  transition: 0.3s;
  cursor: pointer;
  background-color: ${({ active }) =>
    active ? "rgb(223,223,223)" : "inherit"};

  &:hover {
    background-color: rgb(223, 223, 223);
  }
`;

export const ImpactIcon = styled(FiHeart)`
  font-size: 1.25rem;
`;

export const HelpIcon = styled(IoHelpCircleOutline)`
  font-size: 1.25rem;
`;

export const SettingsIcon = styled(IoSettingsOutline)`
  font-size: 1.25rem;
`;

export const CartIcon = styled(IoCartOutline)`
  font-size: 1.25rem;
`;

export const MapIcon = styled(IoMapOutline)`
  font-size: 1.25rem;
`;

export const ExitIcon = styled(IoExitOutline)`
  font-size: 1.25rem;
`;

export const LogInIcon = styled(IoLogInOutline)`
  font-size: 1.25rem;
`;

export const NavName = styled.span`
  font-size: 0.875rem;
`;

export const UserIcon = styled.img`
  width: ${({ expand }) => (expand ? "64px" : "")};
  height: 64px;
  margin: auto;
  border-radius: 100%;
  background-size: contain;
  background-image: url("https://www.tinygraphs.com/squares/tinygraphs?theme=frogideas&numcolors=4&size=220&fmt=svg");
`;
