import React, { useState, useEffect } from "react";
import {
  List,
  ListElement,
  SettingsContainer,
  SingleSettingContainer,
  Subtitle,
  Title,
} from "./Settings.elements";
import { Redirect, Route, Switch, useRouteMatch } from "react-router-dom";
import UserProfile from "./SubSettings/UserProfile";
import Privacy from "./SubSettings/Privacy";
import Other from "./SubSettings/Other";
import ProtectedRoute from "../Auth/ProtectedRoute";
import Admin from "./SubSettings/Admin";
import axios from "axios";

const Settings = () => {
  const { url, path } = useRouteMatch();
  const [isAdmin, setIsAdmin] = useState(false);

  useEffect(() => {
    axios
      .post("/koddler-rest/api/authorization/isAdmin.php", {
        withCredentials: true,
      })
      .then((response) => {
        setIsAdmin(response.data.admin);
      })
      .catch((error) => {});
  }, []);

  return (
    <>
      <Title> Impostazioni </Title>
      <SettingsContainer>
        <Subtitle> Impostazioni account </Subtitle>
        <List>
          <ListElement to={`${url}/user-profile`}> Profilo </ListElement>
          <ListElement to={`${url}/privacy`}> Privacy </ListElement>
          {isAdmin && <ListElement to={`${url}/impact`}> Admin </ListElement>}
          <ListElement to={`${url}/other`}> Altro </ListElement>
        </List>
        <SingleSettingContainer>
          <Switch>
            <Route path={`${path}/user-profile`} component={UserProfile} />
            <Route path={`${path}/privacy`} component={Privacy} />
            <Route path={`${path}/other`} component={Other} />
            <ProtectedRoute
              path={`${path}/impact`}
              component={Admin}
              isAuth={isAdmin}
              redirectPath="/dashboard"
            />
            <Redirect from="/profile" to={`${path}/user-profile`} />
          </Switch>
        </SingleSettingContainer>
      </SettingsContainer>
    </>
  );
};

export default Settings;
