import { Link } from "react-router-dom";
import styled from "styled-components";

export const FormWrapper = styled.div`
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
`;

export const FormOverlay = styled.div`
  min-width: 100%;
  max-width: 100%;
  backdrop-filter: blur(35px);
  background-color: rgba(255, 255, 255, 0.8);
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 0px 2rem;
`;

export const RanNameForm = styled.form`
  align-items: center;
`;

export const RanLayer = styled.div`
  display: flex;
  align-items: center;
`;

export const RanInput = styled.input`
  outline: none;
  width: 80%;
  max-width: 350px;
  min-width: 250px;
  height: 40px;
  border: none;
  margin: 0.5rem 0px;
  background-color: rgb(245, 245, 245);
  border-radius: 8px;
  padding: 0px 1rem;
  transition: all 0.2s ease-in 0s;
`;

export const RanSelect = styled.select`
  outline: none;
  width: 80%;
  max-width: 350px;
  min-width: 250px;
  height: 40px;
  border: none;
  margin: 0.5rem 0px;
  background-color: rgb(245, 245, 245);
  border-radius: 8px;
  padding: 0px 1rem;
  transition: all 0.2s ease-in 0s;
  appearance: none;
  background-image: url("data:image/svg+xml;utf8,<svg fill='black' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
  background-repeat: no-repeat;
  background-position-x: 95%;
  background-position-y: 8px;
`;

export const RanInFlexInput = styled(RanInput)`
  width: ${({ big }) => (big ? "35%" : "25%")};
  min-width: ${({ big }) => (big ? "35%" : "25%")};
  max-width: ${({ big }) => (big ? "35%" : "25%")};

  &:first-child {
    margin-right: 5px;
  }
`;

export const RanCheck = styled.div`
  height: 10px;
  width: 10px;
  background: ${({ allSet }) =>
    allSet ? "rgb(112,237,185)" : "rgb(254, 47, 117)"};
  border-radius: 10px;
  margin-left: 1rem;
`;

export const RanButton = styled.button`
  width: 81%;
  max-width: 350px;
  min-width: 250px;
  height: 40px;
  border: none;
  margin: 1rem 0px;
  border-radius: 8px;
  background-color: rgb(97, 60, 234);
  color: rgb(255, 255, 255);
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease-in 0s;
`;

export const FormFooter = styled.div``;

export const FormHeader = styled.div``;

export const RanP = styled.p`
  padding: 0px 1rem;
  text-align: center;
  font-size: 10px;
  color: rgb(128, 128, 128);
  font-weight: 300;
`;

export const RanH3 = styled.h3`
  color: rgb(102, 102, 102);
  margin-bottom: 2rem;
`;

export const RanH4 = styled.h4`
  color: rgb(128, 128, 128);
  font-weight: bold;
  font-size: 13px;
  margin-top: 2rem;
`;

export const RanLink = styled(Link)`
  color: rgb(255, 141, 141);
  cursor: pointer;
`;
