import { NavLink } from "react-router-dom";
import styled from "styled-components";

export const SettingsContainer = styled.div`
  width: 100%;
  padding: 2rem;
`;

export const Title = styled.h2`
  font-weight: bold;
  font-weight: 700;
  line-height: 1.125;
  font-size: 1.75rem;
  margin: 0;
  padding: 24px 32px;
  border-bottom: 2px solid #e7e9ec;
`;

export const Subtitle = styled.h4`
  font-weight: bold;
  font-weight: 700;
  line-height: 1.125;
  margin: 0;
  padding: 0;
  font-size: 1.25rem;
`;

export const List = styled.div`
  display: flex;
  margin-top: 24px;
`;

export const ListElement = styled(NavLink)`
  font-size: 13px;
  color: #4b4e61;
  font-weight: bold;
  padding: 8px 10px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  margin-right: 15px;
  background-color: transparent;

  &:hover {
    background-color: rgb(248, 248, 248);
  }

  &.active {
    background-color: rgb(248, 248, 248);
  }

  @media screen and(max-width: 820px) {
    margin-top: 10px;
  }
`;

export const SingleSettingContainer = styled.div`
  margin: 32px 0px;
  width: 100%;
  height: auto;
`;

export const SettingWrapper = styled.div`
  padding: 32px 0px;
  border-top: 2px solid #e7e9ec;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
`;

export const SettingInfo = styled.div`
  width: 30%;
  height: 100px;

  @media screen and (max-width: 820px) {
    width: 100%;
  }
`;

export const SettingProps = styled.div`
  flex: 1;
`;

export const SettingTitle = styled.h4`
  margin: 0;
  font-size: 18px;
  color: #262952;
  font-weight: bold;
  padding: 0;
`;

export const SettingSubtitle = styled.p`
  margin: 0;
  font-size: 14px;
  color: #c1c3d0;
  padding: 0;
  max-width: 60%;
`;

export const SettingLabel = styled.label`
  margin-top: 24px;
  display: block;
  margin-bottom: 4px;
  font-size: 14px;
  color: #262952;
  font-weight: bold;

  &:first-child {
    margin-top: 0;
  }
`;

export const SettingInput = styled.input`
  outline: none;
  display: block;
  width: 100%;
  background: #fff;
  border: 2px solid #e7e9ec;
  padding: 8px 16px;
  margin-top: 14px;
  line-height: 22px;
  font-size: 14px;
  font-weight: 500;
  color: #6c7486;
  border-radius: 6px;
  transition: border 0.3s ease;
  max-width: 40%;
`;

export const SettingCheckboxInput = styled.input`
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid #e5e5e5;
  padding: 14px 20px;
  font-size: 14px;
  outline: none;
  margin-bottom: 25px;
  display: inline-block;
  vertical-align: middle;
  margin: 0 !important;
`;

export const SettingCheckboxLabel = styled.label`
  margin: 0 !important;
  margin-left: 8px !important;
  font-size: 14px;
  color: #262952;
  font-weight: bold;
  display: inline-block;
  vertical-align: middle;
`;

export const SettingCheckboxUnderLabel = styled.p`
  display: block;
  margin: 0;
  font-size: 14px;
  color: #c1c3d0;
  padding: 0;
  margin-left: 16px;
`;

export const SaveButton = styled.button`
  width: 100%;
  max-width: 200px;
  min-width: 150px;
  height: 40px;
  border: none;
  margin: 1rem 0px;
  box-shadow: rgb(0 0 0 / 25%) 0px 14px 9px -15px;
  border-radius: 8px;
  background-color: #613cea;
  color: rgb(255, 255, 255);
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease-in 0s;
`;

export const TermsContainer = styled.div`
  width: 40%;
  margin: auto;
  padding: 2rem;
  border-radius: 3px;
  background-color: rgb(248, 248, 248);

  @media screen and (max-width: 820px) {
    width: 100%;
  }
`;

export const TermsTitle = styled.h2`
  color: #000;
  font-weight: bold;
  font-weight: 700;
  line-height: 1.125;
  font-size: clamp(1.5rem, 2.5vw, 2.5rem);
`;

export const TermsText = styled.p``;

export const SwitchInput = styled.input`
  height: 0;
  width: 0;
  visibility: hidden;
`;

export const SwitchLabel = styled.label`
  cursor: pointer;
  text-indent: -9999px;
  width: 200px;
  height: 100px;
  background: grey;
  display: block;
  border-radius: 100px;
  position: relative;

  &:after {
    content: "";
    position: absolute;
    top: 5px;
    left: 5px;
    width: 90px;
    height: 90px;
    background: #fff;
    border-radius: 90px;
    transition: 0.3s;
  }

  &:active:after {
    width: 130px;
  }

  SwitchInput:checked + &:after {
    left: calc(100% - 5px);
    transform: translateX(-100%);
  }
`;
