import { React, Component } from "react";
import {
  FormFooter,
  FormHeader,
  RanSelect,
  FormOverlay,
  FormWrapper,
  RanButton,
  RanCheck,
  RanH3,
  RanH4,
  RanInput,
  RanLayer,
  RanLink,
  RanNameForm,
  RanP,
} from "./Form.elements";
import axios from "axios";
import { ErrorBox } from "../../gloablStyles";

class SignUnForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      name: "",
      address: "",
      email: "",
      password: "",
      date: "",
      gender: "DEFAULT",
      number: "",
      error: "",
    };

    this.handleNameChange = this.handleChange.bind(this, "name");
    this.handleAddressChange = this.handleChange.bind(this, "address");
    this.handleEmailChange = this.handleChange.bind(this, "email");
    this.handlePasswordChange = this.handleChange.bind(this, "password");
    this.handleDateChange = this.handleChange.bind(this, "date");
    this.handleGenderChange = this.handleChange.bind(this, "gender");
    this.handleNumberChange = this.handleChange.bind(this, "number");

    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(keyName, e) {
    this.setState({ [keyName]: e.target.value });
  }

  handleSubmit(e) {
    e.preventDefault();

    var params = new URLSearchParams();
    params.append("full_name", this.state.name);
    params.append("address", this.state.address);
    params.append("email", this.state.email);
    params.append("password", this.state.password);
    params.append("birthday", this.state.date);
    params.append("gender", this.state.gender);
    params.append("phone_number", this.state.number);

    axios
      .post("/koddler-rest/api/authentication/register.php", params)
      .then((response) => {
        window.location.reload();
      })
      .catch((error) => {
        this.setState({ error: "Errore nella richiesta, controlla i dati." });
        setTimeout(() => this.setState({ error: "" }), 5000);
      });
  }

  render() {
    return (
      <FormWrapper>
        {this.state.error ? <ErrorBox>{this.state.error}</ErrorBox> : ""}
        <FormOverlay>
          <FormHeader></FormHeader>
          <RanNameForm onSubmit={this.handleSubmit}>
            <RanH3>Sign Up</RanH3>
            <RanLayer>
              <RanInput
                onChange={this.handleNameChange}
                defaultValue={this.state.name}
                placeholder="Full Name"
                type="text"
                required
              />
              <RanCheck allSet={this.state.name.trim().length > 0} />
            </RanLayer>
            <RanLayer>
              <RanInput
                onChange={this.handleAddressChange}
                defaultValue={this.state.address}
                placeholder="Full Address"
                type="text"
                required
              />
              <RanCheck allSet={this.state.address.trim().length > 0} />
            </RanLayer>
            <RanLayer>
              <RanInput
                onChange={this.handleEmailChange}
                defaultValue={this.state.email}
                placeholder="Email"
                type="email"
                required
              />
              <RanCheck allSet={this.state.email.trim().length > 0} />
            </RanLayer>
            <RanLayer>
              <RanInput
                onChange={this.handlePasswordChange}
                defaultValue={this.state.password}
                pattern="^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*_=+-]).{8,}$"
                placeholder="Password"
                type="password"
                required
              />
              <RanCheck allSet={this.state.password.trim().length > 0} />
            </RanLayer>
            <RanLayer>
              <RanInput
                onChange={this.handleNumberChange}
                defaultValue={this.state.number}
                placeholder="Phone Number"
                pattern="^[0-9]{10,10}$"
                type="text"
                required
              />
              <RanCheck allSet={this.state.number.trim().length > 0} />
            </RanLayer>
            <RanLayer>
              <RanInput
                onChange={this.handleDateChange}
                defaultValue={this.state.date}
                type="date"
                required
              />
              <RanCheck allSet={this.state.date.trim().length > 0} />
            </RanLayer>
            <RanLayer>
              <RanSelect
                onChange={this.handleGenderChange}
                defaultValue={this.state.gender}
                placeholder="Gender"
              >
                <option value="DEFAULT" disabled>
                  {" "}
                  Select your gender{" "}
                </option>
                <option value="n"> Non Binary </option>
                <option value="f"> Female </option>
                <option value="m"> Male </option>
                <option value="o"> Other </option>
              </RanSelect>
              <RanCheck allSet={this.state.gender !== "DEFAULT"} />
            </RanLayer>
            <RanButton>Sign Up</RanButton>
          </RanNameForm>
          <FormFooter>
            <RanP>
              By signin up, You agree to the Privacy Policy
              <br />
              and Terms of Service
            </RanP>
            <RanH4>
              {" "}
              Already have an account? <RanLink to="/signin"> Sign In </RanLink>
            </RanH4>
          </FormFooter>
        </FormOverlay>
      </FormWrapper>
    );
  }
}

export default SignUnForm;
