import React, { useEffect, useState } from "react";
import {
  AssistanceIcon,
  ButtonWrapper,
  Card25,
  Card75,
  CardInfo,
  CardNumber,
  FaqIcon,
  GoIcon,
  HelpIcon,
  HeroBanner,
  HeroButton,
  HeroH2,
  HeroImage,
  HeroImageWrapper,
  HeroP,
  Icon,
  IconWrapper,
  LeftSpace,
  OrdersDashboard,
  RefreshIcon,
  Resizer,
  RfidCard,
  RfidCardWrapper,
  RightSpace,
  Setting,
  SettingButton,
  SettingIconWrapper,
  SettingInfo,
  Settings,
  SettingSubtitle,
  SettingTitle,
  SpanLabel,
  Subtitle,
  TableH2,
  TableSub,
  TableWrapper,
  TDd,
  TDetail,
  TDl,
  TDt,
  TH3,
  TPrice,
  TSmall,
  TStrong,
  TSummary,
} from "./Generics.elements";
import Nature from "../../images/nature.svg";
import axios from "axios";
import EmptyArea from "../../components/EmptyArea/EmptyArea";

const Generics = () => {
  const [data, setData] = useState([]);
  const [cardData, setCardData] = useState([]);

  useEffect(() => {
    axios
      .post("koddler-rest/api/queries/userActions.php", {
        withCredentials: true,
      })
      .then((response) => {
        setData(response.data);
      });

    axios
      .post("koddler-rest/api/queries/cardDetails.php", {
        withCredentials: true,
      })
      .then((response) => {
        setCardData(response.data);
      });
  }, []);

  return (
    <>
      <OrdersDashboard>
        <LeftSpace>
          <RfidCardWrapper>
            <Resizer>
              <Subtitle> La tua carta </Subtitle>
              {cardData.map((carta) => {
                return (
                  <RfidCard>
                    <CardNumber>
                      {carta.tag
                        .replace(/[]/g, "")
                        .replace(/\W/gi, "")
                        .replace(/(.{4})/g, "$1 ")}
                    </CardNumber>
                    <Card75>
                      <SpanLabel>Card holder</SpanLabel>
                      <CardInfo>
                        {carta.name} {carta.surname}
                      </CardInfo>
                    </Card75>
                    <Card25>
                      <SpanLabel>Expires</SpanLabel>
                      <CardInfo>{carta.expiry_date}</CardInfo>
                    </Card25>
                  </RfidCard>
                );
              })}

              <SettingTitle>
                {" "}
                Come vengono usati i dati della carta?{" "}
              </SettingTitle>
              <SettingSubtitle>
                {" "}
                Grazie alla tecnologia RFID, ti basterà avvicinare la tua carta
                allo scanner integrato nella Stazione di Noleggio e Riconsegna e
                sarai pronto al noleggio dei nostri monopattini!{" "}
              </SettingSubtitle>

              <Subtitle> Impostazioni </Subtitle>
              <Settings>
                <Setting>
                  <SettingIconWrapper pos="first">
                    <AssistanceIcon />
                  </SettingIconWrapper>
                  <SettingInfo>
                    <SettingTitle> Contatta l'assistenza </SettingTitle>
                    <SettingSubtitle>
                      {" "}
                      Per contattare l'assistenza clicca qui.{" "}
                    </SettingSubtitle>
                  </SettingInfo>
                  <GoIcon />
                </Setting>
                <Setting>
                  <SettingIconWrapper pos="second">
                    <FaqIcon />
                  </SettingIconWrapper>
                  <SettingInfo>
                    <SettingTitle> F.A.Q. </SettingTitle>
                    <SettingSubtitle>
                      {" "}
                      Consulta le domande piu' frequenti.{" "}
                    </SettingSubtitle>
                  </SettingInfo>
                  <GoIcon />
                </Setting>
                <Setting>
                  <SettingIconWrapper pos="third">
                    <HelpIcon />
                  </SettingIconWrapper>
                  <SettingInfo>
                    <SettingTitle> Aiuto sulla carta </SettingTitle>
                    <SettingSubtitle>
                      {" "}
                      Per leggere la nostra guida cliccare qui.{" "}
                    </SettingSubtitle>
                  </SettingInfo>
                  <GoIcon />
                </Setting>
                <ButtonWrapper>
                  <SettingButton> Lorem ipsum </SettingButton>
                </ButtonWrapper>
              </Settings>
            </Resizer>
          </RfidCardWrapper>
        </LeftSpace>

        <RightSpace>
          <HeroBanner>
            <HeroImageWrapper>
              <HeroImage src={Nature} />
            </HeroImageWrapper>
            <HeroH2> Aiutaci a costruire un mondo eco-friendly! </HeroH2>
            <HeroP>
              {" "}
              Una sensibilità maggiore all’ambiente non può prescindere da un
              ripensamento completo della mobilità. Alcuni segnali sono già
              attivi, soprattutto nella grandi città, dove è in continuo
              incremento l’utilizzo del car sharing. Aiutaci anche tu a
              diminuire l'impatto, noleggiando un monopattino elettrico.
            </HeroP>
            <HeroButton> Scopri di più </HeroButton>
          </HeroBanner>
          <TableWrapper>
            <TableH2>
              Ultime transazioni
              <RefreshIcon />
            </TableH2>
            <TableSub> Today </TableSub>

            {data.length > 0 ? (
              data.map((transazione) => {
                return (
                  <TDetail>
                    <TSummary>
                      <IconWrapper>
                        <Icon />
                      </IconWrapper>
                      <TH3>
                        <TStrong>
                          {" "}
                          Transazione n. {transazione.action_id}#
                          {transazione.rfid_tag}{" "}
                        </TStrong>
                        <TSmall>
                          {" "}
                          {transazione.action_type === "r"
                            ? "Riconsegna"
                            : "Noleggio"}{" "}
                        </TSmall>
                      </TH3>
                      <TPrice>
                        {" "}
                        {transazione.action_cost &&
                          "-" + transazione.action_cost + "€"}
                      </TPrice>
                    </TSummary>
                    <TDl>
                      <div>
                        <TDt> Data e Ora </TDt>
                        <TDd> {transazione.action_datetime} </TDd>
                      </div>
                      <div>
                        <TDt> Carta utilizzata </TDt>
                        <TDd> {transazione.tag} </TDd>
                      </div>
                      <div>
                        <TDt> ID Monopattino </TDt>
                        <TDd> {transazione.rfid_tag} </TDd>
                      </div>
                    </TDl>
                  </TDetail>
                );
              })
            ) : (
              <EmptyArea />
            )}
          </TableWrapper>
        </RightSpace>
      </OrdersDashboard>
    </>
  );
};

export default Generics;
