import React, { Component } from "react";
import {
  SaveButton,
  SettingCheckboxInput,
  SettingCheckboxLabel,
  SettingCheckboxUnderLabel,
  SettingInfo,
  SettingInput,
  SettingLabel,
  SettingProps,
  SettingSubtitle,
  SettingTitle,
  SettingWrapper,
} from "../Settings.elements";
import axios from "axios";

class UserProfile extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  componentDidMount() {
    axios
      .post("koddler-rest/api/queries/userDetails.php", {
        withCredentials: true,
      })
      .then((response) => {
        this.setState(response.data[0]);
      });
  }

  render() {
    return (
      <>
        <SettingWrapper>
          <SettingInfo>
            <SettingTitle> Informazioni Personali </SettingTitle>
            <SettingSubtitle>
              {" "}
              Modifica le tue impostazioni personali come il nome o la data di
              nascita.{" "}
            </SettingSubtitle>
          </SettingInfo>
          <SettingProps>
            <SettingLabel> Nome Completo </SettingLabel>
            <SettingInput type="text" defaultValue={this.state.full_name} />
            <SettingLabel> Data di nascita </SettingLabel>
            <SettingInput
              type="date"
              defaultValue={this.state.birth_date}
              placeholder={this.state.birth_date}
            />
            <SettingLabel> Numero di telefono </SettingLabel>
            <SettingInput
              type="text"
              defaultValue={this.state.phone_number}
              placeholder={this.state.phone_number}
            />
          </SettingProps>
        </SettingWrapper>

        <SettingWrapper>
          <SettingInfo>
            <SettingTitle> Informazioni di casa </SettingTitle>
            <SettingSubtitle>
              {" "}
              Modifica le informazioni relative al tuo indirizzo di casa.{" "}
            </SettingSubtitle>
          </SettingInfo>
          <SettingProps>
            <SettingLabel> Indirizzo di casa </SettingLabel>
            <SettingInput type="text" defaultValue={this.state.address} />
          </SettingProps>
        </SettingWrapper>

        <SettingWrapper>
          <SettingInfo>
            <SettingTitle> Iscrizioni </SettingTitle>
            <SettingSubtitle>
              {" "}
              Modifica le tue iscrizioni come quella alla newsletter{" "}
            </SettingSubtitle>
          </SettingInfo>
          <SettingProps>
            <SettingCheckboxInput
              type="checkbox"
              name="newsletter"
              defaultChecked={this.state.is_newsletter}
            />
            <SettingCheckboxLabel>
              {" "}
              Iscrizione alla newsletter{" "}
            </SettingCheckboxLabel>
            <SettingCheckboxUnderLabel>
              {" "}
              Modifica la tua iscrizione alla newsletter.{" "}
            </SettingCheckboxUnderLabel>
          </SettingProps>
        </SettingWrapper>

        <SaveButton> Salva </SaveButton>
      </>
    );
  }
}

export default UserProfile;
