import axios from "axios";
import { React, useState } from "react";
import { useLocation } from "react-router-dom";
import {
  MapIcon,
  NavUILink,
  Nav,
  NavBrand,
  NavLogoIcon,
  NavLogoText,
  NavName,
  NavWrapper,
  CartIcon,
  SettingsIcon,
  HelpIcon,
  LogInIcon,
  ImpactIcon,
  NavUiLinkQuit,
} from "./Sidebar.elements";

const Sidebar = ({ isLogged }) => {
  const [expand, setExpand] = useState(false);
  const handleExpand = () => setExpand(!expand);
  const { pathname } = useLocation();

  const logOut = () =>
    axios
      .post("/koddler-rest/api/authentication/logout.php", {
        withCredentials: true,
      })
      .then(window.location.replace("/"));

  return (
    <NavWrapper
      onMouseEnter={handleExpand}
      onMouseLeave={handleExpand}
      expand={expand}
    >
      <Nav>
        <div>
          <NavBrand>
            <NavLogoIcon />
            <NavLogoText>Koddler</NavLogoText>
          </NavBrand>
          <div>
            <NavUILink to="/dashboard">
              <MapIcon />
              <NavName> Dashboard </NavName>
            </NavUILink>

            <NavUILink to="/orders">
              <CartIcon />
              <NavName> Transazioni </NavName>
            </NavUILink>

            <NavUILink to="/help">
              <HelpIcon />
              <NavName> Assistenza </NavName>
            </NavUILink>

            <NavUILink to="/profile">
              <SettingsIcon />
              <NavName> Profilo </NavName>
            </NavUILink>
          </div>
        </div>
        <div>
          {isLogged ? (
            <NavUiLinkQuit onClick={logOut}>
              <LogInIcon />
              <NavName> Log Out </NavName>
            </NavUiLinkQuit>
          ) : (
            <NavUILink
              to="/signin"
              isActive={() => ["/signin", "/signup"].includes(pathname)}
            >
              <LogInIcon />
              <NavName> Sign In </NavName>
            </NavUILink>
          )}
        </div>
      </Nav>
    </NavWrapper>
  );
};

export default Sidebar;
