import styled from "styled-components";
import { IoFlashOutline, IoHelpCircleOutline } from "react-icons/io5";
import { CgSmartHomeLight } from "react-icons/cg";
import { FaQuoteRight } from "react-icons/fa";
import { BsArrowRightShort } from "react-icons/bs";
import { FiRefreshCw } from "react-icons/fi";

export const OrdersDashboard = styled.main`
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: space-between;
  flex-flow: row wrap;
  flex-direction: row-reverse;
`;

export const HeroBanner = styled.section`
  background-color: #613cea;
  padding: 2rem;
  border-radius: 10px;
  position: relative;

  @media screen and(max-width: 820px) {
    display: none;
  }
`;

export const HeroImageWrapper = styled.div`
  width: 40%;
  max-width: 350px;
  position: absolute;
  overflow: hidden;
  height: calc(110%);
  top: -10%;
  right: 2rem;
`;

export const HeroImage = styled.img`
  width: 100%;
  height: auto;
`;

export const HeroH2 = styled.h2`
  margin-top: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.1rem;
  color: white;
  margin: 0 0 0.25rem !important;
  font-weight: 400;
`;

export const HeroP = styled.p`
  color: rgba(255, 255, 255, 0.7);
  font-weight: 300;
  line-height: 1.7;
  margin-top: 0;
  max-width: 58%;
`;

export const HeroButton = styled.a`
  display: inline-block;
  padding: 0.75rem 1.5rem;
  line-height: 1.5;
  border: 0;
  background-color: #efefef;
  border-radius: 7px;
  font-weight: 500;
  color: #a3a9b0;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  padding-left: 3rem;
  padding-right: 3rem;
  text-transform: uppercase;
  font-size: 0.875rem;
  transition: transform 0.3s, box-shadow 0.3s;
  background-color: white;
  color: #613cea;
`;

export const LeftSpace = styled.div`
  width: 29%;

  @media screen and (max-width: 1260px) {
    width: 100%;
    margin-bottom: 32px;
  }
`;

export const RightSpace = styled.div`
  width: 69%;
  padding: 2rem;
  max-height: 100%;

  @media screen and (max-width: 1260px) {
    width: 100%;
  }
`;

/*
    background-color: rgb(248,248,248);
    padding: 2rem 4rem;
*/
export const TableWrapper = styled.div`
  margin-top: 2rem;
  border-radius: 10px;
`;

export const TableH2 = styled(HeroH2)`
  color: #000;
  font-weight: bold;
  font-weight: 700;
  line-height: 1.125;
  font-size: clamp(1.5rem, 2.5vw, 2.5rem);
`;

export const TitleH2 = styled(TableH2)`
  text-align: center;
  margin: auto;
`;

export const TableSub = styled.h2`
  margin-top: 0.25em;
  color: #999;
  font-size: clamp(1.125rem, 2.5vw, 1.25rem);
  margin-bottom: 1.5em;
`;

export const TDetail = styled.details`
  border-top: 1px solid #b5bfd9;
  border-bottom: 1px solid #b5bfd9;
`;

export const TSummary = styled.summary`
  background-color: #fff;
  position: relative;
  cursor: pointer;
  padding: 1em 0.5em;
  list-style: none;
  display: flex;
  align-items: center;
`;

export const IconWrapper = styled.span`
  width: 4rem;
  height: 4rem;
  border-radius: 10px;
  background-color: #f3e1e1;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  margin-right: 1.25em;
`;

export const Icon = styled(IoFlashOutline)`
  width: 2.25rem;
  height: 2.25rem;
`;

export const TH3 = styled.h3`
  display: flex;
  flex-direction: column;
`;

export const TStrong = styled.strong`
  font-weight: 700;
`;

export const TSmall = styled.small`
  color: #999;
  font-size: 0.875em;
`;

export const TPrice = styled.span`
  font-weight: 700;
  margin-left: auto;
`;

export const TDl = styled.dl`
  display: flex;
  flex-wrap: wrap;

  div {
    margin-right: 4em;
    margin-bottom: 2em;
  }
`;

export const TDt = styled.dt`
  font-weight: 700;
`;

export const TDd = styled.dd`
  margin: 0;
`;

export const RfidCardWrapper = styled.div`
  zoom: 125%;
  width: 100%;
  height: 100%;
  padding: 8px;
  max-height: 100%;
  background-color: rgb(248, 248, 248);
`;

export const RfidCard = styled.div`
  padding: 18px;
  box-shadow: 1px 1px #aaa3a3;
  position: relative;
  display: flex;
  flex-flow: wrap;
  margin: 16px auto;
  width: inherit;
  height: 190px;
  max-height: 190px;
  background-image: url(https://raw.githubusercontent.com/muhammederdem/credit-card-form/master/src/assets/images/18.jpeg),
    linear-gradient(
      to right bottom,
      #fd696b,
      #fa616e,
      #f65871,
      #f15075,
      #ec4879
    );
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 8px;
  -webkit-transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -moz-transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -ms-transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -o-transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: all 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transform-style: preserve-3d;
  backface-visibility: hidden;
`;

export const CardNumber = styled.p`
  display: block;
  width: 100%;
  word-spacing: 4px;
  font-size: 20px;
  letter-spacing: 2px;
  color: #fff;
  text-align: center;
  margin-bottom: 20px;
  margin-top: 20px;
`;

export const Card75 = styled.div`
  width: 75%;
  float: left;
`;

export const Card25 = styled.div`
  width: 25%;
  float: left;
`;

export const SpanLabel = styled.span`
  font-size: 10px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.8);
  letter-spacing: 1px;
`;

export const CardInfo = styled.p`
  margin-bottom: 0;
  margin-top: 5px;
  font-size: 16px;
  line-height: 18px;
  color: #fff;
  letter-spacing: 1px;
  text-transform: uppercase;
`;

export const Resizer = styled.div`
  width: 320px;
  max-width: 320px;
  margin: 0 auto;

  @media screen and (max-width: 1260px) {
    //todo
  }
`;

export const Subtitle = styled.h3`
  margin-top: 32px;
  border-bottom: 2px solid #e7e9ec;
  padding-bottom: 16px;
`;

export const Settings = styled.div``;

export const Setting = styled.div`
  display: flex;
  padding: 16px 0px;
  width: 100%;
  border-bottom: 2px dashed #e7e9ec;
  align-items: center;

  &:first-child {
    margin-top: -15px;
  }
`;

const colors = {
  first: "#65c6d2",
  second: "#f46f6f",
  third: "#ffd974",
};

export const SettingIconWrapper = styled.div`
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 10px;
  display: flex;
  flex-shrink: 0;
  align-items: center;
  align-self: center;
  justify-content: center;
  background-color: ${({ pos }) => colors[pos]};
`;

export const AssistanceIcon = styled(IoHelpCircleOutline)`
  width: 2rem;
  height: 2rem;
  font-weight: bold;
  color: #fff;
`;

export const FaqIcon = styled(FaQuoteRight)`
  width: 1.35rem;
  height: 1.35rem;
  color: #fff;
`;

export const HelpIcon = styled(CgSmartHomeLight)`
  width: 1.8rem;
  height: 1.8rem;
  color: #fff;
`;

export const GoIcon = styled(BsArrowRightShort)`
  align-self: center;
  width: 1.75rem;
  height: 1.75rem;
  border-radius: 8px;
  color: #35385f;
  border: 1px solid #ebebec;
`;

export const SettingInfo = styled.div`
  flex: 1;
  margin: 0px 15px;
`;

export const SettingTitle = styled.h4`
  margin: 0;
  font-size: 12px;
  color: #262952;
  font-weight: bold;
  padding: 0;
`;

export const SettingSubtitle = styled.p`
  margin: 0;
  font-size: 10px;
  color: #c1c3d0;
  padding: 0;
`;

export const SettingButton = styled.button`
  width: 80%;
  max-width: 350px;
  min-width: 250px;
  height: 40px;
  border: none;
  margin: 1rem 0px;
  box-shadow: rgb(0 0 0 / 25%) 0px 14px 9px -15px;
  border-radius: 8px;
  background-color: #613cea;
  color: rgb(255, 255, 255);
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease-in 0s;
`;

export const ButtonWrapper = styled.div`
  display: flex;
  padding: 16px 0px;
  width: 100%;
  align-items: center;
  justify-content: center;
`;

export const RefreshIcon = styled(FiRefreshCw)`
  align-self: center;
  width: 2.75rem;
  height: 2.75rem;
  border-radius: 8px;
  color: #35385f;
  border: 2px solid #ebebec;
  padding: 10px;
  cursor: pointer;
`;
