import { React, Component } from "react";
import {
  FormFooter,
  FormHeader,
  FormOverlay,
  FormWrapper,
  RanButton,
  RanCheck,
  RanH3,
  RanH4,
  RanInput,
  RanLayer,
  RanLink,
  RanNameForm,
  RanP,
} from "./Form.elements";
import axios from "axios";
import { ErrorBox } from "../../gloablStyles";

class SignInForm extends Component {
  error = false;

  constructor(props) {
    super(props);

    this.state = {
      email: "",
      password: "",
      error: "",
    };

    this.handleEmailChange = this.handleChange.bind(this, "email");
    this.handlePasswordChange = this.handleChange.bind(this, "password");
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(keyName, e) {
    this.setState({ [keyName]: e.target.value });
  }

  handleSubmit(e) {
    e.preventDefault();

    var params = new URLSearchParams();
    params.append("email", this.state.email);
    params.append("password", this.state.password);

    axios
      .post("/koddler-rest/api/authentication/login.php", params, {
        withCredentials: true,
      })
      .then((response) => {
        window.location.reload();
      })
      .catch((error) => {
        console.log(error.stack);
        this.setState({ error: "Errore nella richiesta, controlla i dati." });
        setTimeout(() => this.setState({ error: "" }), 5000);
      });
  }
  render() {
    return (
      <FormWrapper>
        {this.state.error ? <ErrorBox>{this.state.error}</ErrorBox> : ""}
        <FormOverlay>
          <FormHeader></FormHeader>
          <RanNameForm onSubmit={this.handleSubmit}>
            <RanH3>Sign In</RanH3>
            <RanLayer>
              <RanInput
                onChange={this.handleEmailChange}
                defaultValue={this.state.email}
                placeholder="Email"
                type="email"
                required
              />
              <RanCheck allSet={this.state.email.trim().length > 0} />
            </RanLayer>
            <RanLayer>
              <RanInput
                onChange={this.handlePasswordChange}
                defaultValue={this.state.password}
                placeholder="Password"
                type="password"
                required
              />
              <RanCheck allSet={this.state.password.trim().length > 0} />
            </RanLayer>
            <RanButton>Sign In</RanButton>
          </RanNameForm>
          <FormFooter>
            <RanP>
              By signin in, You agree to the Privacy Policy
              <br />
              and Terms of Service
            </RanP>
            <RanH4>
              {" "}
              Don't have an account? <RanLink to="/signup"> Sign Up </RanLink>
            </RanH4>
          </FormFooter>
        </FormOverlay>
      </FormWrapper>
    );
  }
}

export default SignInForm;
